import { merge } from 'lodash';
import { useState } from 'react';
import ReactApexChart from 'react-apexcharts';
// material
import { Card, CardHeader } from '@mui/material';
import { Icon } from '@iconify/react';
import refreshFill from '@iconify/icons-eva/refresh-fill';
import { LoadingButton } from '@mui/lab';
//
import { BaseOptionChart } from '../../charts';
// utils
import { fNumber } from '../../../utils/formatNumber';
import { useDispatch, useSelector } from '../../../redux/store';
import { getAppTirage } from '../../../redux/slices/user';
import useAuth from '../../../hooks/useAuth';

// ----------------------------------------------------------------------

export default function ChartBar() {
  const dispatch = useDispatch();
  const { user } = useAuth();
  const { appTirage } = useSelector((state) => state.user);
  const CHART_DATA = [{ name: 'Montant', data: appTirage?.allTirageMontant }];

  const [load, setLoad] = useState(false);

  const handleLoad = async () => {
    setLoad(true);
    await dispatch(getAppTirage());
    setLoad(false);
  };

  const chartOptions = merge(BaseOptionChart(), {
    colors: ['#00a0ea'],
    stroke: { show: false },
    plotOptions: {
      bar: { horizontal: true, barHeight: '30%' }
    },
    tooltip: {
      y: {
        formatter(val) {
          return `${fNumber(val)} ${user.currency_code}`;
        }
      }
    },
    xaxis: {
      categories: appTirage?.allTirage
    }
  });

  return (
    <Card>
      <CardHeader
        title="Tirages historiques"
        subheader="montant de fiches par tirages"
        action={
          <LoadingButton
            onClick={handleLoad}
            loading={load}
            variant="contained"
            startIcon={<Icon icon={refreshFill} />}
          >
            Refresh
          </LoadingButton>
        }
      />
      {appTirage && <ReactApexChart type="bar" series={CHART_DATA} options={chartOptions} height={365} />}
    </Card>
  );
}
