import { merge } from 'lodash';
import { useState } from 'react';
import ReactApexChart from 'react-apexcharts';
// material
import { Card, CardHeader } from '@mui/material';
import { Icon } from '@iconify/react';
import refreshFill from '@iconify/icons-eva/refresh-fill';
import { LoadingButton } from '@mui/lab';
//
import { BaseOptionChart } from '../../charts';
// utils
import { fNumber } from '../../../utils/formatNumber';
import { useDispatch, useSelector } from '../../../redux/store';
import { getAppLotto } from '../../../redux/slices/user';

// ----------------------------------------------------------------------

export default function ChartDonut() {
  const dispatch = useDispatch();
  const { appLotto } = useSelector((state) => state.user);

  const [load, setLoad] = useState(false);

  const handleLoad = async () => {
    setLoad(true);
    await dispatch(getAppLotto());
    setLoad(false);
  };

  const chartOptions = merge(BaseOptionChart(), {
    labels: appLotto?.allLotto,
    stroke: { show: false },
    legend: { horizontalAlign: 'center' },
    plotOptions: { pie: { donut: { size: '90%' } } }
  });

  return (
    <Card>
      <CardHeader
        title="Options historiques"
        subheader="toutes les montants"
        action={
          <LoadingButton
            onClick={handleLoad}
            loading={load}
            variant="contained"
            startIcon={<Icon icon={refreshFill} />}
          >
            Refresh
          </LoadingButton>
        }
      />
      {appLotto && <ReactApexChart type="donut" series={appLotto?.allLottoBoule} options={chartOptions} width={400} />}
    </Card>
  );
}
