import { merge } from 'lodash';
import { useState } from 'react';
import ReactApexChart from 'react-apexcharts';
// material
import { Card, CardHeader } from '@mui/material';
// material
import { useTheme } from '@mui/material/styles';
import { Icon } from '@iconify/react';
import refreshFill from '@iconify/icons-eva/refresh-fill';
import { LoadingButton } from '@mui/lab';
//
import { BaseOptionChart } from '../../charts';
// utils
import { fNumber } from '../../../utils/formatNumber';
import { useDispatch, useSelector } from '../../../redux/store';
import { getAppGpc } from '../../../redux/slices/user';
import useAuth from '../../../hooks/useAuth';

// ----------------------------------------------------------------------

export default function ChartColumnMultiple() {
  const theme = useTheme();
  const { user } = useAuth();
  const { appGpc } = useSelector((state) => state.user);
  const CHART_DATA = [
    { name: 'Montant', data: appGpc?.montant },
    { name: 'Balance', data: appGpc?.gain },
    { name: 'Perte', data: appGpc?.perte }
  ];
  const dispatch = useDispatch();

  const [load, setLoad] = useState(false);

  const handleLoad = async () => {
    setLoad(true);
    await dispatch(getAppGpc());
    setLoad(false);
  };

  const chartOptions = merge(BaseOptionChart(), {
    stroke: { show: true, width: 2, colors: ['transparent'] },
    colors: [theme.palette.chart.violet[0], theme.palette.primary.main, theme.palette.chart.red[0]],
    xaxis: {
      categories: appGpc?.month
    },
    tooltip: {
      y: {
        formatter(val) {
          return `${fNumber(val)} ${user.currency_code}`;
        }
      }
    },
    plotOptions: {
      bar: { columnWidth: '58%' }
    }
  });

  return (
    <Card>
      <CardHeader
        title="Rapports de chaque mois"
        subheader="toutes vos montants, gains et pertes de l'annee"
        action={
          <LoadingButton
            onClick={handleLoad}
            loading={load}
            variant="contained"
            startIcon={<Icon icon={refreshFill} />}
          >
            Refresh
          </LoadingButton>
        }
      />
      {appGpc && <ReactApexChart type="bar" series={CHART_DATA} options={chartOptions} height={320} />}
    </Card>
  );
}
