import { merge } from 'lodash';
import { useState } from 'react';
import ReactApexChart from 'react-apexcharts';
// material
import { Card, CardHeader } from '@mui/material';
//
import { Icon } from '@iconify/react';
import refreshFill from '@iconify/icons-eva/refresh-fill';
import { LoadingButton } from '@mui/lab';
import { BaseOptionChart } from '../../charts';
// utils
import { fNumber } from '../../../utils/formatNumber';
import { useDispatch, useSelector } from '../../../redux/store';
import { getAppFiche } from '../../../redux/slices/user';

// ----------------------------------------------------------------------

export default function AppAreaInstalled() {
  const dispatch = useDispatch();
  const { appFiche } = useSelector((state) => state.user);
  const [load, setLoad] = useState(false);

  const handleLoad = async () => {
    setLoad(true);
    await dispatch(getAppFiche());
    setLoad(false);
  };

  const chartOptions = merge(BaseOptionChart(), {
    xaxis: {
      categories: appFiche?.month
    }
  });

  return (
    <Card>
      <CardHeader
        title="Fiches historiques"
        subheader="nombres de fiches par date"
        action={
          <LoadingButton
            onClick={handleLoad}
            loading={load}
            variant="contained"
            startIcon={<Icon icon={refreshFill} />}
          >
            Refresh
          </LoadingButton>
        }
      />
      {appFiche && <ReactApexChart type="line" series={appFiche?.data} options={chartOptions} height={320} />}
    </Card>
  );
}
