import { useState, useEffect } from 'react';
// material
import { Card, Container, Grid } from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../../redux/store';
import { getRapports, getAgents, getSurcussales, getInfoTirages } from '../../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
// hooks
import useSettings from '../../../hooks/useSettings';
// components
import Page from '../../../components/Page';
import HeaderBreadcrumbs from '../../../components/HeaderBreadcrumbs';
import { RapportListToolbar } from '../../../components/_dashboard/rapport/list';
import { fDate } from '../../../utils/formatTime';
import { fCurrency } from '../../../utils/formatNumber';
import { AgentRap, SupRap, TotalRap } from '../../../components/_dashboard/general-app';
import useAuth from '../../../hooks/useAuth';
// ----------------------------------------------------------------------

const TIRAGE = [
  { id: 'NYM', name: 'New York Midi' },
  { id: 'NYS', name: 'New York Soir' },
  { id: 'FLM', name: 'Florida Midi' },
  { id: 'FLS', name: 'Florida Soir' },
  { id: 'GGMT', name: 'Georgia Matin' },
  { id: 'GGMD', name: 'Georgia Soir' },
  { id: 'GGS', name: 'Georgia Nuit' },
  { id: 'TXMA', name: 'Texas Matin' },
  { id: 'TXM', name: 'Texas Midi' },
  { id: 'TXS', name: 'Texas Soir' },
  { id: 'CGM', name: 'Chicago Midi' },
  { id: 'CGS', name: 'Chicago Soir' },
  { id: 'LCM', name: 'Lave chodyè Matin' },
  { id: 'LCS', name: 'Lave chodyè Soir' },
  { id: 'TNM', name: 'Tennessee Matin' },
  { id: 'TNMD', name: 'Tennessee Midi' },
  { id: 'TNS', name: 'Tennessee Soir' },
  { id: 'NJM', name: 'New Jersey Matin' },
  { id: 'NJMM', name: 'New Jersey Midi' },
  { id: 'NJS', name: 'New Jersey Soir' },
  { id: 'ANA', name: 'Anguilla AM (10H)' },
  { id: 'ANM', name: 'Anguilla Midi' },
  { id: 'ANS', name: 'Anguilla Soir' },
  { id: 'ANN', name: 'Anguilla Nuit' },
  { id: 'RL', name: 'Real' },
  { id: 'PRM', name: 'Primera Midi' },
  { id: 'PRS', name: 'Primera Soir' },
  { id: 'SUA', name: 'Suerte AM' },
  { id: 'SUS', name: 'Suerte Soir' },
  { id: 'LK', name: 'Loteka' },
  { id: 'KL', name: 'King Lottery Noche' },
  { id: 'NA', name: 'Nacional Midi' },
  { id: 'NAS', name: 'Nacional Soir' },
  { id: 'QPL', name: 'Quiniela palé' }
];

export default function Rapport({
  handleInvoice,
  value,
  value1,
  value2,
  value3,
  value4,
  value5,
  setValue,
  setValue1,
  handleChangeMultiple,
  setValue3,
  setValue4,
  setValue5
}) {
  const { user } = useAuth();
  const { themeStretch } = useSettings();
  const dispatch = useDispatch();
  const { rapports, agents, surcussales, infotirages } = useSelector((state) => state.user);
  const [loading, setLoading] = useState(false);
  const isRapportNotFound = rapports?.length === 0 || rapports?.agentsupRapport?.quantite === 0;
  const onChangeDate1 = (datavalue) => {
    setValue(datavalue);
  };
  const onChangeDate2 = (datavalue) => {
    setValue1(datavalue);
  };

  const handleGo = async () => {
    setLoading(true);
    try {
      await Promise.all([dispatch(getRapports(fDate(value), fDate(value1), value2, value3, value4, value5))]);
    } catch (error) {
      alert(JSON.stringify(error));
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    dispatch(getAgents());
    dispatch(getSurcussales());
    dispatch(getInfoTirages());
  }, [dispatch]);

  return (
    <Page title="Rapport: List | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Liste des rapports"
          links={[{ name: 'Dashboard', href: PATH_DASHBOARD.root }, { name: 'Rapport' }]}
        />
        <Grid container spacing={3}>
          <Grid item xs={12} md={12}>
            <Card>
              <RapportListToolbar
                value={value}
                value1={value1}
                value2={value2}
                value3={value3}
                value4={value4}
                value5={value5}
                handleChangeMultiple={handleChangeMultiple}
                setValue3={setValue3}
                setValue4={setValue4}
                setValue5={setValue5}
                onChangeDate1={onChangeDate1}
                onChangeDate2={onChangeDate2}
                handleGo={handleGo}
                handleInvoice={handleInvoice}
                isRapportNotFound={isRapportNotFound}
                loading={loading}
                setLoading={setLoading}
                agents={agents}
                surcussales={surcussales}
                infoTirages={user?.role !== 'superuser' ? infotirages : TIRAGE}
              />
            </Card>
          </Grid>

          <Grid item xs={12} md={12}>
            <AgentRap list={rapports?.agentRapport} user={user} />
          </Grid>

          {rapports?.supRapport?.length > 0 && (
            <Grid item xs={12} md={12}>
              <SupRap list={rapports?.supRapport} user={user} />
            </Grid>
          )}

          <Grid item xs={12} md={12}>
            <TotalRap rapport={rapports?.agentsupRapport} />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
}
