import { useEffect } from 'react';
// material
import { Box, Card, Typography, Stack } from '@mui/material';
// utils
import { fCurrency } from '../../../utils/formatNumber';
// ----------------------------------------------------------------------

export default function AppTotalAnother({
  text = 'Total',
  value,
  bgColor = '#6C00FF',
  color = 'white',
  fontSize = 11,
  type = 'num'
}) {
  return (
    <Card sx={{ display: 'flex', alignItems: 'center', p: 3, backgroundColor: bgColor, color }}>
      <Box sx={{ flexGrow: 1 }}>
        <Typography variant="subtitle2" sx={{ fontSize }}>
          {text}
        </Typography>
        <Typography variant="h4">{type === 'float' ? fCurrency(value) || 0 : value || 0}</Typography>
      </Box>
    </Card>
  );
}
