import { merge } from 'lodash';
import { useState } from 'react';
import ReactApexChart from 'react-apexcharts';
// material
import { Card, CardHeader } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import { Icon } from '@iconify/react';
import refreshFill from '@iconify/icons-eva/refresh-fill';
import { LoadingButton } from '@mui/lab';
//
import { BaseOptionChart } from '../../charts';
// utils
import { fNumber } from '../../../utils/formatNumber';
import { useDispatch, useSelector } from '../../../redux/store';
import { getAppGain } from '../../../redux/slices/user';
import useAuth from '../../../hooks/useAuth';
// ----------------------------------------------------------------------

export default function ChartColumnNegative() {
  const theme = useTheme();
  const { user } = useAuth();
  const { appGain } = useSelector((state) => state.user);
  const dispatch = useDispatch();
  const [load, setLoad] = useState(false);

  const handleLoad = async () => {
    setLoad(true);
    await dispatch(getAppGain());
    setLoad(false);
  };

  const CHART_DATA = [
    {
      name: 'Balance',
      data: appGain?.gain
    }
  ];

  const chartOptions = merge(BaseOptionChart(), {
    stroke: { show: false },
    yaxis: {
      labels: {
        formatter: (y) => `${y.toFixed(0)} ${user.currency_code}`
      }
    },
    xaxis: {
      type: 'datetime',
      categories: appGain?.date
    },
    plotOptions: {
      bar: {
        columnWidth: '58%',
        colors: {
          ranges: [
            { from: 100000, to: 1000000000, color: theme.palette.chart.yellow[0] },
            { from: -1000000000, to: 0, color: theme.palette.chart.red[0] }
          ]
        }
      }
    }
  });

  return (
    <Card>
      <CardHeader
        title="Historiques de vos gains"
        subheader="toutes vos gains de ce mois"
        action={
          <LoadingButton
            onClick={handleLoad}
            loading={load}
            variant="contained"
            startIcon={<Icon icon={refreshFill} />}
          >
            Refresh
          </LoadingButton>
        }
      />
      {appGain && <ReactApexChart type="bar" series={CHART_DATA} options={chartOptions} height={320} />}
    </Card>
  );
}
