import React, { useState } from 'react';
import Rapport from './RapportComp';
import Invoice from './RapportInvoice';

export default function Rapports() {
  const [invoiceActive, setInvoiceActive] = useState(false);
  const [value, setValue] = useState(new Date());
  const [value1, setValue1] = useState(new Date());
  const [value2, setValue2] = useState([]);
  const [value3, setValue3] = useState(0);
  const [value4, setValue4] = useState(0);
  const handleInvoice = () => {
    setInvoiceActive(!invoiceActive);
  };

  const handleChangeMultiple = (event) => {
    const { value } = event.target;
    setValue2(value);
  };

  return (
    <>
      {invoiceActive === false ? (
        <Rapport
          handleInvoice={handleInvoice}
          value={value}
          value1={value1}
          value2={value2}
          value3={value3}
          value4={value4}
          setValue={setValue}
          setValue1={setValue1}
          setValue2={setValue2}
          setValue3={setValue3}
          setValue4={setValue4}
          handleChangeMultiple={handleChangeMultiple}
        />
      ) : (
        <Invoice
          handleInvoice={handleInvoice}
          value={value}
          value1={value1}
          value2={value2}
          value3={value3}
          value4={value4}
          setValue={setValue}
          setValue1={setValue1}
          setValue2={setValue2}
          setValue3={setValue3}
          setValue4={setValue4}
        />
      )}
    </>
  );
}
