import { v4 as uuidv4 } from 'uuid';
import { random, sum } from 'lodash';
// material
import { styled, useTheme } from '@mui/material/styles';
import {
  Box,
  Grid,
  Card,
  Table,
  Divider,
  TableRow,
  Container,
  TableBody,
  TableHead,
  TableCell,
  Typography,
  TableContainer
} from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../../redux/store';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
// utils
import { fCurrency } from '../../../utils/formatNumber';
// hooks
import useSettings from '../../../hooks/useSettings';
// components
import Page from '../../../components/Page';
import Label from '../../../components/Label';
import Scrollbar from '../../../components/Scrollbar';
import HeaderBreadcrumbs from '../../../components/HeaderBreadcrumbs';
import { InvoiceToolbar } from '../../../components/_dashboard/rapport/invoice';
import { fDateTime, fDate } from '../../../utils/formatTime';
import useAuth from '../../../hooks/useAuth';
// ----------------------------------------------------------------------

const RowResultStyle = styled(TableRow)(({ theme }) => ({
  '& td': {
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1)
  }
}));

// ----------------------------------------------------------------------

export default function RapportInvoice({ handleInvoice, value, value1, value2, value3, value4 }) {
  const { themeStretch } = useSettings();
  const { user } = useAuth();
  const theme = useTheme();

  const today = fDateTime(new Date());

  const { rapports, infotirages, agents, surcussales } = useSelector((state) => state.user);

  return (
    <Page title="Rapport: Details | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Rapports Details"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            {
              name: 'Rapport',
              href: PATH_DASHBOARD.rapport
            },
            { name: 'Details' }
          ]}
        />

        <InvoiceToolbar
          invoice={rapports}
          handleInvoice={handleInvoice}
          value={fDate(value)}
          value1={fDate(value1)}
          value2={value2}
          value3={value3}
          value4={value4}
          companyname={user?.companyname}
          userPhoto={user?.user_photo}
          surcussales={surcussales}
          infotirages={infotirages}
          agents={agents}
        />

        <Card sx={{ pt: 5, px: 5 }}>
          <Grid container>
            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Box sx={{ textAlign: { sm: 'left' }, width: 120, height: 50 }}>
                <img alt="LOGO" src={user?.user_photo} style={{ paddingLeft: 18, paddingRight: 18 }} />
              </Box>
            </Grid>
            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Box sx={{ textAlign: { sm: 'right' } }} />
            </Grid>
            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Box sx={{ textAlign: { sm: 'left' } }}>
                <Typography variant="h6">{user?.companyname}</Typography>
              </Box>
            </Grid>
            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Box sx={{ textAlign: { sm: 'right' } }}>
                <Typography variant="h6">{fDateTime(today)}</Typography>
              </Box>
            </Grid>
            <Grid item xs={12} sm={12} sx={{ mb: 5 }}>
              <Box sx={{ textAlign: { sm: 'center' } }}>
                <Typography variant="h3">Rapport de Vente</Typography>
              </Box>
            </Grid>

            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Typography paragraph variant="overline" sx={{ color: 'text.disabled' }}>
                De
              </Typography>
              <Typography variant="body2">{fDate(value)}</Typography>
            </Grid>

            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Typography paragraph variant="overline" sx={{ color: 'text.disabled' }}>
                a
              </Typography>
              <Typography variant="body2">{fDate(value1)}</Typography>
            </Grid>
            <Grid item xs={12} sm={4} sx={{ mb: 5 }}>
              <Typography paragraph variant="overline">
                {value2?.length > 0 ? 'Tirage: ' : 'Tirage: Tout'}
                {value2?.map((item, index) => (
                  <span key={item.id}>
                    {infotirages?.find((e) => e.id === item)?.name}
                    {index === value2?.length - 1 ? null : ', '}
                  </span>
                ))}
              </Typography>
            </Grid>
            <Grid item xs={12} sm={4} sx={{ mb: 5 }}>
              <Typography paragraph variant="overline">
                {value4 !== 0
                  ? 'Vendeur: '
                  : `Vendeur: (${rapports?.agentRapport?.length}) vendeur${
                      rapports?.agentRapport?.length > 1 ? 's' : ''
                    }`}
                {agents?.find((e) => e.id === value4)?.first_name} {agents?.find((e) => e.id === value4)?.last_name}
              </Typography>
            </Grid>
            <Grid item xs={12} sm={4} sx={{ mb: 5 }}>
              <Typography paragraph variant="overline">
                {value3 !== 0 ? 'Succursale: ' : 'Succursale: Tout'}{' '}
                {surcussales?.find((e) => e.id === value3)?.addresse_complet}
              </Typography>
            </Grid>
          </Grid>

          <Typography paragraph variant="overline">
            Agent
          </Typography>

          <Scrollbar>
            <TableContainer sx={{ minWidth: 960 }}>
              <Table>
                <TableHead
                  sx={{
                    borderBottom: (theme) => `solid 1px ${theme.palette.divider}`,
                    '& th': { backgroundColor: 'transparent' }
                  }}
                >
                  <TableRow>
                    <TableCell align="left">Agent</TableCell>
                    <TableCell align="center">Tfiches</TableCell>
                    <TableCell align="center">Tfiches gagnant</TableCell>
                    <TableCell align="center">Vente</TableCell>
                    <TableCell align="center">A payé</TableCell>
                    <TableCell align="center">%Agent</TableCell>
                    <TableCell align="center">balance sans %agent</TableCell>
                    <TableCell align="right">balance avec %agent</TableCell>
                  </TableRow>
                </TableHead>

                <TableBody>
                  {rapports?.agentRapport?.map((row, index) => (
                    <TableRow
                      key={index}
                      sx={{
                        borderBottom: (theme) => `solid 1px ${theme.palette.divider}`
                      }}
                    >
                      <TableCell>{row.agent_name}</TableCell>
                      <TableCell align="center">{row.quantite}</TableCell>
                      <TableCell align="center">{row.fgagnant}</TableCell>
                      <TableCell align="center">{parseFloat(row.montant_total).toFixed(2)}</TableCell>
                      <TableCell align="center">{parseFloat(row.perte).toFixed(2)}</TableCell>
                      <TableCell align="center">{parseFloat(row.commission).toFixed(2)}</TableCell>
                      <TableCell align="center">{parseFloat(row.gain_s_percent_agent).toFixed(2)}</TableCell>
                      <TableCell align="right">
                        <Label
                          variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                          color={(row?.gain_a_percent_agent < 0 && 'error') || 'success'}
                        >
                          {parseFloat(row?.gain_a_percent_agent).toFixed(2)}
                        </Label>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Scrollbar>
          {rapports?.supRapport?.length > 0 && (
            <>
              <br />
              <br />
              <Typography paragraph variant="overline">
                Superviseur
              </Typography>

              <Scrollbar>
                <TableContainer sx={{ minWidth: 960 }}>
                  <Table>
                    <TableHead
                      sx={{
                        borderBottom: (theme) => `solid 1px ${theme.palette.divider}`,
                        '& th': { backgroundColor: 'transparent' }
                      }}
                    >
                      <TableRow>
                        <TableCell align="left">Superviseur</TableCell>
                        <TableCell align="center">Total Vente</TableCell>
                        <TableCell align="right">Total Pourcentage</TableCell>
                      </TableRow>
                    </TableHead>

                    <TableBody>
                      {rapports?.supRapport?.map((row, index) => (
                        <TableRow
                          key={index}
                          sx={{
                            borderBottom: (theme) => `solid 1px ${theme.palette.divider}`
                          }}
                        >
                          <TableCell>{row.supervisor_name}</TableCell>
                          <TableCell align="center">{parseFloat(row.montant_total).toFixed(2)}</TableCell>
                          <TableCell align="right">{parseFloat(row.commission).toFixed(2)}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              </Scrollbar>
            </>
          )}

          <br />
          <br />
          <Typography paragraph variant="overline">
            Total
          </Typography>

          <Scrollbar>
            <TableContainer sx={{ minWidth: 960 }}>
              <Table>
                <RowResultStyle>
                  {/* <TableCell colSpan={1} /> */}
                  <TableCell align="left">
                    <Typography variant="subtitle2">Tfiche: {rapports?.agentsupRapport?.quantite}</Typography>
                  </TableCell>
                  <TableCell align="left">
                    <Typography variant="subtitle2">Tfiche gagnant: {rapports?.agentsupRapport?.fgagnant}</Typography>
                  </TableCell>
                  <TableCell align="left">
                    <Typography variant="subtitle2">vente: {rapports?.agentsupRapport?.montant_total}</Typography>
                  </TableCell>
                  <TableCell align="left">
                    <Typography variant="subtitle2">A payé: {rapports?.agentsupRapport?.perte}</Typography>
                  </TableCell>
                </RowResultStyle>
                <RowResultStyle>
                  {/* <TableCell colSpan={1} /> */}
                  <TableCell align="left">
                    <Typography variant="subtitle2">%Agent: {rapports?.agentsupRapport?.commissionA}</Typography>
                  </TableCell>
                  <TableCell align="left">
                    <Typography variant="subtitle2">
                      balance sans %agent: {rapports?.agentsupRapport?.gain_s_percent_agent}
                    </Typography>
                  </TableCell>
                  <TableCell align="left">
                    <Typography variant="subtitle2">
                      balance avec %agent: {rapports?.agentsupRapport?.gain_a_percent_agent}
                    </Typography>
                  </TableCell>
                </RowResultStyle>
                <RowResultStyle>
                  {/* <TableCell colSpan={1} /> */}
                  <TableCell align="left">
                    <Typography variant="subtitle2">%Superviseur: {rapports?.agentsupRapport?.commissionS}</Typography>
                  </TableCell>
                  <TableCell align="left">
                    <Typography variant="subtitle2">
                      balance sans %superviseur: {rapports?.agentsupRapport?.gain_s_percent_supervisor}
                    </Typography>
                  </TableCell>
                  <TableCell align="left">
                    <Typography variant="subtitle2">
                      balance avec %superviseur: {rapports?.agentsupRapport?.gain_a_percent_supervisor}
                    </Typography>
                  </TableCell>
                </RowResultStyle>
              </Table>
            </TableContainer>
          </Scrollbar>

          <Divider sx={{ mt: 5 }} />

          <Grid container>
            <Grid item xs={12} md={9} sx={{ py: 3 }}>
              <Typography variant="subtitle2">NOTES</Typography>
              <Typography variant="body2">
                Ce rapport est generé a partir des fiches non supprimées sur le systeme!
              </Typography>
            </Grid>
          </Grid>
        </Card>
      </Container>
    </Page>
  );
}
