import PropTypes from 'prop-types';
// @mui
import { styled } from '@mui/material/styles';
import { Typography, Button, Card, Box, Stack } from '@mui/material';
import moment from 'moment';
import 'moment/locale/fr';
moment.locale('fr');
/* eslint import/newline-after-import: "off" */
// ----------------------------------------------------------------------

const RootStyle = styled(Card)(({ theme }) => ({
  boxShadow: theme.customShadows.z8,
  backgroundColor: 'white',
  height: '100%',
  display: 'flex',
  textAlign: 'center',
  alignItems: 'center',
  justifyContent: 'space-between'
}));

// ----------------------------------------------------------------------

Tirage.propTypes = {
  id: PropTypes.number,
  list: PropTypes.array
};

export default function Tirage({ id, list }) {
  const sortedList = [...list];

  sortedList.sort((a, b) => {
    // Define the order of times of day
    const timeOfDayOrder = ['Soir', 'Midi', 'Matin'];

    // Get the time of day for both items
    const timeOfDayA = a.name;
    const timeOfDayB = b.name;

    // Compare the positions of the time of day in the order
    return timeOfDayOrder.indexOf(timeOfDayA) - timeOfDayOrder.indexOf(timeOfDayB);
  });

  return (
    <RootStyle>
      <Box
        sx={{
          width: 200,
          display: 'flex',
          alignItems: 'center'
        }}
      >
        <img alt="LOGO" src={`/static/icons/${id}.png`} style={{ paddingLeft: 18, paddingRight: 18 }} />
      </Box>
      <Box sx={{ p: 1 }}>
        {sortedList?.map((item) => (
          <>
            <Typography variant="subtitle2" sx={{ py: 1 }}>
              {moment(item?.date).calendar(null, {
                lastDay: '[Hier]',
                sameDay: "[Aujourd'hui]",
                nextDay: '[Demain]',
                lastWeek: 'dddd',
                nextWeek: 'dddd',
                sameElse: 'L'
              })}{' '}
              {item.name}
            </Typography>
            <Stack
              sx={{
                display: 'flex',
                flexDirection: 'row',
                textAlign: 'center',
                alignItems: 'center',
                justifyContent: 'space-between'
              }}
            >
              <Box
                sx={{
                  padding: 0.2,
                  backgroundColor: '#143fff',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  borderRadius: 50
                }}
              >
                <Typography variant="subtitle2" sx={{ color: 'white', px: 0.5 }}>
                  {item.tirage_1}
                </Typography>
              </Box>
              <Box
                sx={{
                  padding: 0.2,
                  backgroundColor: '#ff9612',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  borderRadius: 50
                }}
              >
                <Typography variant="subtitle2" sx={{ color: 'white', px: 0.5 }}>
                  {item.tirage_2}
                </Typography>
              </Box>
              <Box
                sx={{
                  padding: 0.2,
                  backgroundColor: '#00a0ea',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  borderRadius: 50
                }}
              >
                <Typography variant="subtitle2" sx={{ color: 'white', px: 0.5 }}>
                  {item.tirage_3}
                </Typography>
              </Box>
            </Stack>
          </>
        ))}
      </Box>
    </RootStyle>
  );
}
